   color: rgb(249, 52, 80);\n  }\n  .item-status-control {\n    flex-direction: row;\n  }\n  .item-status-control-refund {\n    width: 160px; \n    height: 50px;\n    border-style: solid; \n    border-color: rgb(244, 39, 100); \n    justify-content: center;\n  }\n  .item-status-control-refund-text {\n    text-align: center; \n    color: rgb(244, 39, 100);\n  }\n  .item-status-control-logistic {\n    width: 160px; \n    height: 50px;\n    border-style: solid; \n    border-color: #000; \n    justify-content: center; \n    margin-left: 22px; \n    flex-direction: column;\n    display: -webkit-box;\n    display: -webkit-flex;\n    display: -ms-flexbox;\n    display: flex;\n  }\n  .item-status-control-logistic-text {\n    text-align: center;\n  }\n  .loading {\n    width: 750px;\n    height: 100px;\n    background-color: transparent;\n    align-items: center;\n    justify-content: center;\n  }\n  /*end 订单列表item样式*/\n\n\n  /*start 顶部信息样式*/\n  .list-header-wrap {\n    width: 750px;\n  }\n  .list-header {\n    flex-direction: row;\n    align-items: center;\n    height: 80px;\n    background-color: #efefef;\n  }\n  .list-header-text {\n    text-align: center;\n    font-size: 28px;\n    line-height: 30px;\n  }\n  .list-header-text-grey {\n    color: rgb(158, 158, 158);\n  }\n  .list-header-div {\n    height: 80px;\n    flex: 1;\n    align-items: center;\n    justify-content: center;\n    position: relative;\n    flex-direction: row;\n  }\n  .category-tab-bar {\n    border-bottom-width: 3px;\n    border-bottom-style: solid;\n    border-bottom-color: #000;\n    width: 120px;\n    position: absolute;\n    bottom: 0px;\n    left: 34px;\n  }\n  .category-header {\n    flex-direction: row;\n    align-items: center;\n    height: 94px;\n    border-style: solid; \n    border-color: rgb(239, 239, 239); \n    background-color: #fff;\n  }\n  .category-header-div {\n    height: 94px;\n    flex: 1;\n    align-items: center;\n    position: relative;\n    justify-content: center;\n  }\n  .ca